.header-input {
  text-align: center;
  background-color: teal;
  padding: 20px;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form input {
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form .btn-search {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid teal;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
}

.btn-search:hover {
  color: #fff;
  background-color: teal;
  border-color: #fff;
}

.main-magic {
  margin-top: 30px;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  flex: calc(25% - 30px);
  margin-bottom: 30px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  object-fit: cover;
  height: 80%;
  display: block;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: teal;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, color .3s;
  display: none;
}

.load-more:hover {
  color: teal;
  background-color: #fff;
  border-color: teal;
}

.info {
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 14px;
}

